// fonts
$default-fonts: Arial, sans-serif;

@include font('Calibri', 'Calibri');
@include font('Calibri Light', 'Calibri-Light');
@include font('Calibri Bold', 'Calibri-Bold');

@mixin calibri {
	font-family: 'Calibri', $default-fonts; }

@mixin calibri-light {
	font-family: 'Calibri Light', $default-fonts; }

@mixin calibri-bold {
	font-family: 'Calibri Bold', $default-fonts; }

// colors
// $black: #000

@mixin trs {
	transition: all 0.2s; }


/* ===================================
	 Main Styles
	 @mixin ================================== */
body {
	position: relative;
	min-width: 1200px;
	@include calibri;
	font-size: 16px;
	color: #001d31;
	line-height: 1.25;
	letter-spacing: 0.02em; }

.wrapper {
	position: relative;
	overflow: hidden;
	margin: 0 auto;
	min-height: 100%; }

.container {
	margin-left: auto;
	margin-right: auto;
	padding-left: 20px;
	padding-right: 20px;
	max-width: 1568px;
	min-width: 1200px;
	@include pie-clearfix; }


/* Helpers
	----------------------------------- */
button {
	background-color: transparent; }

a:hover, a:focus {
	text-decoration: none; }

a {
	color: inherit;
	text-decoration: none; }

table {
	vertical-align: top; }

img {
	max-width: 100%;
	vertical-align: top; }

.icon {
	display: inline-block;
	vertical-align: middle; }

.text-left {
	text-align: left; }

.text-center {
	position: relative;
	text-align: center; }

.text-right {
	text-align: right; }

.is-hidden {
	display: none; }

.vertical {
	display: inline-block;
	vertical-align: middle; }

.cfx {
	@include pie-clearfix; }

// *
// 	+selection
// 		color: #fff
// 		background-color: $red

.btn {
	display: inline-block;
	padding-left: 30px;
	padding-right: 30px;
	height: 36px;
	white-space: nowrap;
	text-align: center;
	font-size: 14px;
	color: #ffffff;
	line-height: 36px;
	letter-spacing: 0.1em;
	text-transform: uppercase;
	&_red {
		border: 1px solid #b91443;
		background-color: #b91443;
		@include trs;
		&:hover {
			background-color: transparent; } }
	&_white {
		border: 1px solid #FFFFFF;
		background-color: transparent;
		@include trs;
		&:hover {
			border-color: #b91443;
			background-color: #b91443; } }
	&_red-t {
		color: #b91443;
		border: 1px solid #b91443;
		@include trs;
		&:hover {
			color: #ffffff;
			background-color: #b91443; } }
	&_lg {
		padding-left: 48px;
		padding-right: 48px;
		border-width: 2px;
		height: 66px;
		font-size: 22px;
		line-height: 66px; }
	&_md {
		height: 51px;
		padding-left: 0;
		padding-right: 0;
		width: 278px;
		font-size: 18px;
		line-height: 51px; } }

.title {
	font-size: 30px;
	text-align: center;
	color: #b91443;
	text-transform: uppercase;
	letter-spacing: 0.24em;
	&_black {
		color: #000000; }
	&_white {
		color: #ffffff; } }



/* ===================================
	 Header
	 @mixin ================================== */
.header {
	position: fixed;
	z-index: 99;
	left: 0;
	top: 0;
	right: 0;
	color: #ffffff;
	@include trs;
	&.is-active {
		height: 77px; }
	.container {
		position: relative; }
	&__top {
		position: relative;
		height: 48px;
		text-align: center;
		background-color: #000000;
		@include trs; }
	&.is-active &__top {
		height: 77px;
		background-color: rgba(0, 0, 0, .8); }
	&__nav {
		position: relative;
		z-index: 1;
		padding-top: 14px;
		@include trs; }
	&.is-active &__nav {
		transform: translateY(13px); }
	&__phone {
		position: absolute;
		z-index: 1;
		top: 13px;
		left: 20px;
		font-size: 21px;
		color: #ffe9be;
		line-height: 1;
		@include trs; }
	&.is-active &__phone {
		pointer-events: none;
		opacity: 0; }
	&__btn {
		position: absolute;
		z-index: 1;
		top: 5px;
		right: 20px;
		@include trs; }
	&.is-active &__btn {
		transform: translateY(13px); }
	&__bot {
		position: relative; }
	&.is-active &__bot {
		pointer-events: none; }
	&__logo {
		float: left;
		margin-top: 13px;
		@include trs; }
	&.is-active &__logo {
		transform: scale(0.57) translateY(-175px) translateX(-70px);
		pointer-events: auto; }
	&__info {
		float: right;
		padding-top: 60px;
		@include pie-clearfix;
		@include trs; }
	&.is-active &__info {
		opacity: 0;
		pointer-events: none; }
	&__block {
		float: right;
		font-size: 20px; }
	&__links {
		@include pie-clearfix; }
	&__link {
		float: left;
		text-decoration: underline;
		color: #ffffff;
		@include trs;
		&:hover {
			color: #f2d3b5; }
		& + & {
			margin-left: 15px; } } }

.soc {
	float: left;
	@include pie-clearfix;
	&__item {
		float: left;
		font-size: 0;
		margin-right: 15px;
		.icon {
			display: inline-block; }
		.icon + .icon {
			display: none; }
		&:hover {
			.icon {
				display: none; }
			.icon + .icon {
				display: inline-block; } } } }

.nav {
	display: inline-block;
	vertical-align: middle;
	font-size: 16px;
	line-height: 20px;
	text-transform: uppercase;
	@include pie-clearfix;
	@include r(1439) {
		position: relative;
		left: -30px; }
	&__link {
		float: left;
		margin-left: 22px;
		margin-right: 22px;
		color: #ffffff;
		@include trs;
		&:hover {
			color: #7c7c7c; }
		@include r(1439) {
			margin-left: 11px;
			margin-right: 11px; } } }




/* ===================================
	 Footer
	 @mixin ================================== */
.footer {
	padding-top: 25px;
	padding-bottom: 65px;
	background-color: #30363c;
	color: #ffffff;
	&__logo {
		float: left;
		margin-right: 90px;
		@include r(1439) {
			margin-right: 50px; } }
	&__info {
		overflow: hidden;
		padding-top: 40px;
		@include pie-clearfix; }
	&__soc {
		float: left;
		@include pie-clearfix;
		&-item {
			float: left;
			@include vertical;
			width: 47px;
			height: 47px;
			background-color: transparent;
			border: 1px solid #fff;
			border-radius: 50%;
			@include trs;
			&:hover {
				border-color: transparent;
				background-color: #3a5a99; }
			& + & {
				margin-left: 14px;
				&:hover {
					background-color: #5a7da4; }
				.icon {
					position: relative;
					top: -1px;
					left: 2px; } } } }
	&__infos {} }

.infos {
	float: right;
	font-size: 20px;
	margin-right: -3%;
	width: calc(100% - 175px);
	@include pie-clearfix;
	@include r(1439) {
		margin-right: 0;
		width: calc(100% - 135px); }
	&__col {
		position: relative;
		float: left;
		width: 25%;
		text-align: center;
		& + & {
			border-left: 1px solid #595e63; }
		&-in {
			display: inline-block;
			vertical-align: top;
			text-align: left; } }
	&__subtext {
		text-indent: -12px; } }


/* ===================================
	 Main
	 @mixin ================================== */

/* Start
	----------------------------------- */
.start {
	position: relative;
	height: 0;
	min-height: 725px;
	color: #ffffff;
	background-color: transparent;
	&:after {
		pointer-events: none;
		content: '';
		position: absolute;
		top: -48px;
		left: 0;
		right: 0;
		height: 300px;
		width: 100%;
		background: url('../img/bg/header-bg.png') left top repeat-x; }
	&__container {
		height: 100%;
		@include vertical; }
	&__in {
		position: relative;
		display: inline-block;
		vertical-align: middle; }
	&__title {
		font-size: 49px;
		margin-bottom: 25px; } }

/* Service
	----------------------------------- */
.service {
	position: relative;
	padding-top: 75px;
	padding-bottom: 75px;
	background-color: #e3e4e6;
	&:before {
		pointer-events: none;
		content: '';
		position: absolute;
		top: -125px;
		left: 0;
		right: 0;
		height: 125px;
		background: url('../img/bg/start-bg.png') center center no-repeat;
		background-size: auto 100%; }
	&__btn {
		margin-top: 60px; }
	&__list {
		margin-top: 35px;
		margin-left: -20px;
		margin-right: -20px;
		@include pie-clearfix;
		@include r(1599) {
			margin-left: 20px;
			margin-right: 20px;
			width: 70%;
			.slick-list {
				overflow: visible;
				margin-right: -25%; }
			.slick-prev {
				display: none!important; } } }
	.slick-next {
		display: none;
		position: absolute;
		z-index: 1;
		top: 0;
		bottom: 0;
		right: 0;
		width: 150px;
		color: #b91443;
		background: url(../img/bg/slider-bg.png) -50px top repeat-y;
		@include r(1599) {
			display: block; } }
	&__item {
		float: left;
		width: 20%;
		padding-left: 20px;
		padding-right: 20px; }
	&__image {
		display: block;
		position: relative;
		margin-left: -10px;
		margin-bottom: 35px;
		padding: 12px;
		width: 284px;
		height: 284px;
		border-radius: 50%;
		border: 2px solid #fff;
		@include trs;
		&:hover {
			border-color: #b91443; } }
	&__img {
		min-width: 100%;
		max-width: none;
		&-wrap {
			overflow: hidden;
			width: 100%;
			height: 100%;
			border-radius: 50%; } }
	&__subtitle {
		position: relative;
		margin-bottom: 25px;
		padding-left: 10px;
		font-size: 22px;
		color: #b91443;
		text-transform: uppercase;
		@include r(1599) {
			padding-right: 50px; }
		&:before {
			content: '';
			top: 10px;
			left: -20px;
			position: absolute;
			width: 10px;
			height: 10px;
			background-color: #b91443;
			transform: rotate(45deg); } }
	&__text {
		padding-left: 10px;
		padding-right: 20px;
		@include r(1599) {
			max-width: 350px; } } }

/* Why
	----------------------------------- */
.why {
	padding-top: 75px;
	padding-bottom: 75px;
	background: #efd4bd url('../img/bg/why-bg.jpg') center top no-repeat;
	background-size: auto 100%;
	@include r(1439) {
		background-position: 60% center; }
	&__list {
		margin-top: 70px;
		font-size: 0; }
	&__item {
		display: inline-block;
		margin-bottom: 10px;
		padding-right: 70px;
		width: 30%;
		color: #001d31;
		vertical-align: top;
		@include r(1439) {
			width: 33.3%; }
		&:nth-child(2) {
			width: 35%;
			margin-right: 20%;
			@include r(1439) {
				width: 33.3%; }
			& ~ .why__item {
				vertical-align: bottom; } }
		&:nth-child(4) {
			padding-right: 30px;
			@include r(1439) {
				padding-right: 40px; } } }
	&__subtitle {
		position: relative;
		font-size: 21px;
		text-transform: uppercase;
		letter-spacing: 0.04em;
		&:before {
			content: '';
			position: absolute;
			bottom: -11px;
			left: 0;
			width: 36px;
			height: 2px;
			background-color: #b91443; } }
	&__text {
		margin-top: 22px;
		font-size: 16px; }
	&__btn {
		position: relative;
		z-index: 6;
		margin-top: 70px; } }

.text {
	display: none;
	position: absolute;
	z-index: 5;
	top: 50px;
	left: 0;
	right: 0;
	margin-left: -100px;
	margin-right: -100px;
	background-color: #ffffff;
	box-shadow: 0 5px 5px rgba(0, 1, 1, 0.13);
	@include r(1800) {
		margin-left: auto;
		margin-right: auto; }
	&__container {
		padding-top: 150px;
		padding-bottom: 80px;
		min-width: 0; }
	&__row {
		padding-top: 75px;
		padding-bottom: 50px;
		text-align: left;
		@include pie-clearfix;
		& + & {
			border-top: 1px solid rgba(0,0,0,.2);
			padding-top: 50px;
			padding-bottom: 0; } }
	&__col2 {
		float: left;
		width: auto;
		& + & {
			float: none;
			overflow: hidden; } }
	&__col3 {
		float: left;
		width: 33.33%; }
	&__list {
		padding-right: 25px; }
	&__item {
		position: relative;
		padding-left: 20px;
		& + & {
			margin-top: 15px; }
		&:before {
			position: absolute;
			left: 0;
			top: 7px;
			width: 5px;
			height: 5px;
			border-radius: 50%;
			background-color: #b91443;
			content: ''; } }
	&__image {
		position: relative;
		float: left;
		margin-right: 50px;
		@include r(1600) {
			margin-right: 20px; }
		&:before {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			background-color: transparent;
			@include trs; }
		&-icon {
			opacity: 0;
			width: 48px;
			height: 48px;
			border-radius: 50%;
			border: 2px solid #b91544;
			@include vcenter;
			@include trs;
			.icon {
				position: relative;
				top: -2px;
				left: -1px; } }
		&:hover {
			&:before {
				background-color: rgba(255, 255, 255, .3); } }
		&:hover &-icon {
			opacity: 1; } }
	&__text {
		& + & {
			margin-top: 25px; } } }

/* Work
	----------------------------------- */
.work {
	position: relative;
	background: url('../img/bg/work-bg.jpg') center center no-repeat;
	background-size: cover;
	padding-bottom: 6px;
	min-height: 600px;
	&:before {
		content: '';
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		height: 6px;
		background-color: #ffffff; }
	&-for {
		&__item {
			display: none;
			&.is-active {
				display: block; } }
		&__container {
			height: 594px;
			min-height: 594px;
			@include vertical;
			text-align: left; }
		&__in {
			margin-top: -60px;
			display: inline-block;
			vertical-align: middle;
			padding-left: 60px; }
		&__title {
			position: relative;
			font-size: 30px;
			color: #b91443;
			text-transform: uppercase;
			&:before {
				content: '';
				top: 15px;
				left: -40px;
				position: absolute;
				width: 10px;
				height: 10px;
				background-color: #b91443;
				transform: rotate(45deg); } }
		&__text {
			margin-top: 25px;
			max-width: 500px;
			font-size: 16px; } }
	&-nav {
		background-color: rgba(0, 0, 0, .8);
		&__container {
			font-size: 0; }
		&__item {
			position: relative;
			cursor: pointer;
			display: inline-block;
			margin-right: -1px;
			padding-top: 35px;
			width: 20%;
			height: 120px;
			text-align: center;
			font-size: 16px;
			color: #ffffff;
			line-height: 1.06;
			text-transform: uppercase;
			letter-spacing: 0.1em;
			@include trs;
			&:hover {
				color: #848484; }
			& + & {
				&:before {
					position: absolute;
					left: 0;
					top: 30px;
					height: 63px;
					width: 1px;
					background-color: #828281;
					content: ''; } }
			&.is-active {
				color: #b91443;
				background-color: #ffffff;
				z-index: 1;
				&:before {
					content: none;
					display: none; }
				&:after {
					position: absolute;
					top: -8px;
					left: 0;
					right: 0;
					background-color: #ffffff;
					height: 8px;
					content: ''; } } } } }

/* Spec
	----------------------------------- */
.spec {
	padding-top: 55px;
	padding-bottom: 100px;
	background-color: transparent;
	min-height: 670px;
	&__title {
		margin-bottom: 55px;
		color: #000000; }
	&__block {
		position: relative;
		margin-left: 50%;
		padding: 50px 100px 60px 60px;
		color: #ffffff;
		background-color: rgba(185, 20, 67, .8);
		@include r(1439) {
			padding-right: 50px; }
		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 100%;
			bottom: 0;
			width: 2600px;
			background-color: rgba(185, 20, 67, .8); } }
	&__subtitle {
		padding-right: 20px;
		font-size: 26px;
		text-transform: uppercase;
		letter-spacing: 0.08em; }
	&__text {
		margin-top: 30px;
		font-size: 18px; }
	&__btn {
		margin-top: 45px;
		width: 244px;
		&:hover {
			color: #b91443;
			background-color: #ffffff;
			border-color: #ffffff; } } }

/* Doc
	----------------------------------- */
.desc {
	position: relative;
	opacity: 0;
	pointer-events: none;
	padding-top: 40px;
	padding-bottom: 40px;
	@include trs;
	&.is-active {
		opacity: 1;
		pointer-events: auto; }
	.desc__arr {
		position: relative;
		&:before {
			content: '';
			position: absolute;
			top: -39px;
			left: 10%;
			margin-top: -25px;
			margin-left: -30px;
			@include arr(60,25,#ffffff,t);
			@include trs; } }
	&[data-index='2'] &__arr {
		&:before {
			left: 30%; } }
	&[data-index='3'] &__arr {
		&:before {
			left: 50%; } }
	&[data-index='4'] &__arr {
		&:before {
			left: 70%; } }
	&[data-index='5'] &__arr {
		&:before {
			left: 90%; } }
	&__top {
		position: relative;
		text-align: center;
		letter-spacing: 0.08em;
		padding-bottom: 55px; }
	&__prev {
		cursor: pointer;
		position: absolute;
		top: 25px;
		left: -20px;
		padding-left: 20px;
		padding-right: 20px;
		color: #b91443;
		@include trs;
		&:hover {
			color: #959595; } }
	&__next {
		cursor: pointer;
		position: absolute;
		top: 25px;
		padding-left: 20px;
		padding-right: 20px;
		right: -20px;
		color: #b91443;
		@include trs;
		&:hover {
			color: #959595; } }
	&__title {
		margin-bottom: 20px;
		font-size: 30px;
		color: #b91443;
		text-transform: uppercase;
		letter-spacing: 0.24em; }
	&__subtitle {
		@include calibri-bold;
		font-size: 16px;
		text-transform: uppercase;
		&-normal {
			margin-left: 5px;
			@include calibri; } }
	&__bot {
		display: table;
		width: 100%;
		@include pie-clearfix; }
	&__col {
		display: table-cell;
		padding-right: 40px;
		vertical-align: top;
		height: 475px;
		& + & {
			padding-top: 50px;
			padding-bottom: 50px;
			padding-right: 0;
			border-top: 1px solid #ccc;
			border-bottom: 1px solid #ccc;
			p {
				padding-right: 20px;
				+ p {
					margin-top: 25px; } } }
		&-in {
			overflow: auto;
			height: 375px;
			&::-webkit-scrollbar {
				width: 2px; }
			&::-webkit-scrollbar-track {
				background-color: #e3e4e6;
				border: none; }
			&::-webkit-scrollbar-thumb {
				background-color: #b91443; }
			&::-webkit-scrollbar-thumb:hover {
				background-color: #b91443; } } }
	&__img {
		max-width: none; }
	&__close {
		display: block;
		margin-top: 40px;
		margin-left: auto;
		margin-right: auto;
		width: 216px;
		height: 50px;
		font-size: 16px;
		text-transform: uppercase;
		line-height: 50px;
		border: 1px solid rgba(0, 0, 0, .3);
		@include trs;
		&:hover {
			color: #b91443;
			border-color: #b91443; } } }

.doc {
	padding-top: 110px;
	padding-bottom: 100px;
	background-color: transparent;
	min-height: 554px;
	&__container {
