/* Request
	----------------------------------- */
.request {
	padding-top: 90px;
	padding-bottom: 105px;
	background-color: transparent;
	min-height: 767px;
	&__title {
		color: #ffffff; } }

.form {
	margin-top: 45px;
	margin-left: auto;
	margin-right: auto;
	max-width: 595px;
	letter-spacing: 0.08em;
	&__field {
		position: relative;
		@include pie-clearfix;
		& + & {
			margin-top: 7px; }
		&_md {
			margin-top: 17px!important; }
		&_lg {
			margin-top: 36px!important; } }
	.icon-required {
		position: absolute;
		top: 0;
		left: 100%;
		margin-left: 15px;
		font-size: 30px;
		color: #ffffff; }
	&__input {
		display: block;
		padding: 0 13px;
		width: 100%;
		height: 40px;
		font-size: 16px;
		color: rgba(0, 29, 49, .9);
		line-height: 40px;
		background-color: #ffffff;
		border: 1px solid #d0d0d0;
		text-transform: uppercase;
		@include placeholder-color(rgba(0, 29, 49, .9));
		&:focus ~ .form__tooltip {
			opacity: 1; } }
	&__area {
		display: block;
		padding: 10px 13px;
		height: 125px;
		vertical-align: top;
		width: 100%;
		font-size: 16px;
		color: rgba(0, 29, 49, .9);
		line-height: 1.5;
		text-transform: uppercase;
		resize: none;
		border: 1px solid #d0d0d0;
		@include placeholder-color(rgba(0, 29, 49, .9)); }
	&__tooltip {
		position: absolute;
		top: 0;
		left: 100%;
		padding: 15px 35px;
		white-space: nowrap;
		margin-left: 40px;
		font-size: 18px;
		line-height: 1;
		background-color: rgba(255, 255, 255, .5);
		border-radius: 5px;
		pointer-events: none;
		opacity: 0;
		letter-spacing: -0.02em;
		@include trs;
		@include r(1439) {
			padding-left: 20px;
			padding-right: 20px;
			width: 245px;
			white-space: normal; }
		&:before {
			position: absolute;
			left: -8px;
			top: 15px;
			@include arr(8,16,rgba(255, 255, 255, .5),l);
			content: ''; } }
	.previous-file {
		position: absolute;
		top: 100%;
		left: 0;
		display: block;
		color: #ffffff;
		width: 100%; }
	.remove-btn {
		margin-left: 10px;
		color: #ffffff; }
	.upload-btn {
		cursor: pointer;
		width: 218px;
		height: 43px;
		border: 1px solid white;
		font-size: 14px;
		color: #ffffff;
		line-height: 43px;
		text-transform: uppercase;
		@include trs; }
	.upload-button {
		&:hover {
			.upload-btn {
				color: #b91443;
				background-color: #ffffff; } } }
	input[type='file'] {
		cursor: pointer; }
	&__col {
		width: 50%;
		float: left; }
	&__check {
		display: none;
		&-label {
			position: relative;
			display: block;
			margin-top: 2px;
			padding-top: 12px;
			float: right;
			padding-left: 5px;
			cursor: pointer;
			font-size: 18px;
			color: #ffffff;
			text-transform: uppercase;
			&:before {
				position: absolute;
				top: 2px;
				left: -55px;
				width: 33px;
				height: 33px;
				background-color: #ffffff;
				border: 1px solid #d0d0d0;
				content: ''; }
			&:after {
				position: absolute;
				left: -45px;
				top: 15px;
				content: '';
				width: 15px;
				height: 10px;
				background: url('../img/svg/check-original.svg') center center no-repeat;
				opacity: 0;
				@include trs; } }
		&:checked + &-label {
			&:after {
				opacity: 1; } } }
	&__captcha {
		float: right;
		margin-right: 25px;
		width: 148px;
		height: 40px; }
	&__rel {
		position: relative;
		display: inline-block; }
	&__code {
		margin-left: 25px;
		width: 148px; }
	&__arrow {
		position: absolute;
		top: 9px;
		left: -10px;
		font-size: 18px;
		color: #ffffff; }
	&__submit {
		display: block;
		margin-left: auto;
		margin-right: auto;
		width: 244px;
		height: 50px;
		font-size: 16px;
		color: #ffffff;
		text-align: center;
		text-transform: uppercase;
		border: 1px solid #fff;
		@include trs;
		&:hover {
			background-color: #ffffff;
			color: #b91443; } }
	.form-error {
		display: none; }
	.form__error {
		position: absolute;
		top: 100%;
		left: 50%;
		transform: translateX(-50%);
		width: auto;
		margin-top: 20px;
		padding: 15px 35px;
		white-space: nowrap;
		font-size: 18px;
		line-height: 1;
		background-color: rgba(255, 255, 255, .5);
		border-radius: 5px;
		pointer-events: none;
		text-transform: uppercase;
		opacity: 0;
		color: #b91443;
		@include trs;
		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 50%;
			margin-left: -8px;
			margin-top: -8px;
			@include arr(16,8,rgba(255, 255, 255, .5),t); } }
	.has-error ~ .form__field {
		.form__error {
			opacity: 1; } } }

/* Contact
	----------------------------------- */
.contact {
	padding-top: 100px;
	padding-bottom: 100px;
	&__title {
