#map {
	width: 100%;
	height: 409px; }


/* ===================================
	 Other Styles
	 @mixin ================================== */
.fadeInLeft2 {
	animation-name: fadeInLeft2; }

@-webkit-keyframes fadeInLeft2 {
	from {
		opacity: 0;
		transform: translate3d(-10%, 0, 0); }
	to {
		opacity: 1;
		transform: none; } }
