/* =================================== Main Styles @mixin ================================== */
body { position: relative; min-width: 1200px; font-family: "Calibri", Arial, sans-serif; font-size: 16px; color: #001d31; line-height: 1.25; letter-spacing: 0.02em; }

.wrapper { position: relative; overflow: hidden; margin: 0 auto; min-height: 100%; }

.container { margin-left: auto; margin-right: auto; padding-left: 20px; padding-right: 20px; max-width: 1568px; min-width: 1200px; }

.container:after { content: ""; display: table; clear: both; }

/* Helpers ----------------------------------- */
button { background-color: transparent; }

a:hover, a:focus { text-decoration: none; }

a { color: inherit; text-decoration: none; }

table { vertical-align: top; }

img { max-width: 100%; vertical-align: top; }

.icon { display: inline-block; vertical-align: middle; }

.text-left { text-align: left; }

.text-center { position: relative; text-align: center; }

.text-right { text-align: right; }

.is-hidden { display: none; }

.vertical { display: inline-block; vertical-align: middle; }

.cfx:after { content: ""; display: table; clear: both; }

.btn { display: inline-block; padding-left: 30px; padding-right: 30px; height: 36px; white-space: nowrap; text-align: center; font-size: 14px; color: #ffffff; line-height: 36px; letter-spacing: 0.1em; text-transform: uppercase; }

.btn_red { border: 1px solid #b91443; background-color: #b91443; transition: all 0.2s; }

.btn_red:hover { background-color: transparent; }

.btn_white { border: 1px solid #FFFFFF; background-color: transparent; transition: all 0.2s; }

.btn_white:hover { border-color: #b91443; background-color: #b91443; }

.btn_red-t { color: #b91443; border: 1px solid #b91443; transition: all 0.2s; }

.btn_red-t:hover { color: #ffffff; background-color: #b91443; }

.btn_lg { padding-left: 48px; padding-right: 48px; border-width: 2px; height: 66px; font-size: 22px; line-height: 66px; }

.btn_md { height: 51px; padding-left: 0; padding-right: 0; width: 278px; font-size: 18px; line-height: 51px; }

.title { font-size: 30px; text-align: center; color: #b91443; text-transform: uppercase; letter-spacing: 0.24em; }

.title_black { color: #000000; }

.title_white { color: #ffffff; }

/* =================================== Header @mixin ================================== */
.header { position: fixed; z-index: 99; left: 0; top: 0; right: 0; color: #ffffff; transition: all 0.2s; }

.header.is-active { height: 77px; }

.header .container { position: relative; }

.header__top { position: relative; height: 48px; text-align: center; background-color: #000000; transition: all 0.2s; }

.header.is-active .header__top { height: 77px; background-color: rgba(0, 0, 0, 0.8); }

.header__nav { position: relative; z-index: 1; padding-top: 14px; transition: all 0.2s; }

.header.is-active .header__nav { transform: translateY(13px); }

.header__phone { position: absolute; z-index: 1; top: 13px; left: 20px; font-size: 21px; color: #ffe9be; line-height: 1; transition: all 0.2s; }

.header.is-active .header__phone { pointer-events: none; opacity: 0; }

.header__btn { position: absolute; z-index: 1; top: 5px; right: 20px; transition: all 0.2s; }

.header.is-active .header__btn { transform: translateY(13px); }

.header__bot { position: relative; }

.header.is-active .header__bot { pointer-events: none; }

.header__logo { float: left; margin-top: 13px; transition: all 0.2s; }

.header.is-active .header__logo { transform: scale(0.57) translateY(-175px) translateX(-70px); pointer-events: auto; }

.header__info { float: right; padding-top: 60px; transition: all 0.2s; }

.header__info:after { content: ""; display: table; clear: both; }

.header.is-active .header__info { opacity: 0; pointer-events: none; }

.header__block { float: right; font-size: 20px; }

.header__links:after { content: ""; display: table; clear: both; }

.header__link { float: left; text-decoration: underline; color: #ffffff; transition: all 0.2s; }

.header__link:hover { color: #f2d3b5; }

.header__link + .header__link { margin-left: 15px; }

.soc { float: left; }

.soc:after { content: ""; display: table; clear: both; }

.soc__item { float: left; font-size: 0; margin-right: 15px; }

.soc__item .icon { display: inline-block; }

.soc__item .icon + .icon { display: none; }

.soc__item:hover .icon { display: none; }

.soc__item:hover .icon + .icon { display: inline-block; }

.nav { display: inline-block; vertical-align: middle; font-size: 16px; line-height: 20px; text-transform: uppercase; }

.nav:after { content: ""; display: table; clear: both; }

@media only screen and (max-width: 1439px) { .nav { position: relative; left: -30px; } }

.nav__link { float: left; margin-left: 22px; margin-right: 22px; color: #ffffff; transition: all 0.2s; }

.nav__link:hover { color: #7c7c7c; }

@media only screen and (max-width: 1439px) { .nav__link { margin-left: 11px; margin-right: 11px; } }

/* =================================== Footer @mixin ================================== */
.footer { padding-top: 25px; padding-bottom: 65px; background-color: #30363c; color: #ffffff; }

.footer__logo { float: left; margin-right: 90px; }

@media only screen and (max-width: 1439px) { .footer__logo { margin-right: 50px; } }

.footer__info { overflow: hidden; padding-top: 40px; }

.footer__info:after { content: ""; display: table; clear: both; }

.footer__soc { float: left; }

.footer__soc:after { content: ""; display: table; clear: both; }

.footer__soc-item { float: left; font-size: 0; text-align: center; width: 47px; height: 47px; background-color: transparent; border: 1px solid #fff; border-radius: 50%; transition: all 0.2s; }

.footer__soc-item:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }

.footer__soc-item:hover { border-color: transparent; background-color: #3a5a99; }

.footer__soc-item + .footer__soc-item { margin-left: 14px; }

.footer__soc-item + .footer__soc-item:hover { background-color: #5a7da4; }

.footer__soc-item + .footer__soc-item .icon { position: relative; top: -1px; left: 2px; }

.infos { float: right; font-size: 20px; margin-right: -3%; width: calc(100% - 175px); }

.infos:after { content: ""; display: table; clear: both; }

@media only screen and (max-width: 1439px) { .infos { margin-right: 0; width: calc(100% - 135px); } }

.infos__col { position: relative; float: left; width: 25%; text-align: center; }

.infos__col + .infos__col { border-left: 1px solid #595e63; }

.infos__col-in { display: inline-block; vertical-align: top; text-align: left; }

.infos__subtext { text-indent: -12px; }

/* =================================== Main @mixin ================================== */
/* Start ----------------------------------- */
.start { position: relative; height: 0; min-height: 725px; color: #ffffff; background-color: transparent; }

