@media only screen and (max-width: 1599px) { .service .slick-next { display: block; } }

.service__item { float: left; width: 20%; padding-left: 20px; padding-right: 20px; }

.service__image { display: block; position: relative; margin-left: -10px; margin-bottom: 35px; padding: 12px; width: 284px; height: 284px; border-radius: 50%; border: 2px solid #fff; transition: all 0.2s; }

.service__image:hover { border-color: #b91443; }

