.why__text { margin-top: 22px; font-size: 16px; }

.why__btn { position: relative; z-index: 6; margin-top: 70px; }

.text { display: none; position: absolute; z-index: 5; top: 50px; left: 0; right: 0; margin-left: -100px; margin-right: -100px; background-color: #ffffff; box-shadow: 0 5px 5px rgba(0, 1, 1, 0.13); }

@media only screen and (max-width: 1800px) { .text { margin-left: auto; margin-right: auto; } }

.text__container { padding-top: 150px; padding-bottom: 80px; min-width: 0; }

.text__row { padding-top: 75px; padding-bottom: 50px; text-align: left; }

.text__row:after { content: ""; display: table; clear: both; }

.text__row + .text__row { border-top: 1px solid rgba(0, 0, 0, 0.2); padding-top: 50px; padding-bottom: 0; }

.text__col2 { float: left; width: auto; }

.text__col2 + .text__col2 { float: none; overflow: hidden; }

.text__col3 { float: left; width: 33.33%; }

.text__list { padding-right: 25px; }

.text__item { position: relative; padding-left: 20px; }

.text__item + .text__item { margin-top: 15px; }

.text__item:before { position: absolute; left: 0; top: 7px; width: 5px; height: 5px; border-radius: 50%; background-color: #b91443; content: ''; }

.text__image { position: relative; float: left; margin-right: 50px; }

@media only screen and (max-width: 1600px) { .text__image { margin-right: 20px; } }

.text__image:before { content: ''; position: absolute; left: 0; top: 0; right: 0; bottom: 0; background-color: transparent; transition: all 0.2s; }

.text__image-icon { opacity: 0; width: 48px; height: 48px; border-radius: 50%; border: 2px solid #b91544; position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); transition: all 0.2s; }

.text__image-icon .icon { position: relative; top: -2px; left: -1px; }

.text__image:hover:before { background-color: rgba(255, 255, 255, 0.3); }

.text__image:hover .text__image-icon { opacity: 1; }

.text__text + .text__text { margin-top: 25px; }

/* Work ----------------------------------- */
.work { position: relative; background: url("../img/bg/work-bg.jpg") center center no-repeat; background-size: cover; padding-bottom: 6px; min-height: 600px; }

.work:before { content: ''; position: absolute; bottom: 0; left: 0; right: 0; height: 6px; background-color: #ffffff; }

.work-for__item { display: none; }

.work-for__item.is-active { display: block; }

.work-for__container { height: 594px; min-height: 594px; font-size: 0; text-align: center; text-align: left; }

.work-for__container:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }

.work-for__in { margin-top: -60px; display: inline-block; vertical-align: middle; padding-left: 60px; }

.work-for__title { position: relative; font-size: 30px; color: #b91443; text-transform: uppercase; }

.work-for__title:before { content: ''; top: 15px; left: -40px; position: absolute; width: 10px; height: 10px; background-color: #b91443; transform: rotate(45deg); }

.work-for__text { margin-top: 25px; max-width: 500px; font-size: 16px; }

.work-nav { background-color: rgba(0, 0, 0, 0.8); }

.work-nav__container { font-size: 0; }

.work-nav__item { position: relative; cursor: pointer; display: inline-block; margin-right: -1px; padding-top: 35px; width: 20%; height: 120px; text-align: center; font-size: 16px; color: #ffffff; line-height: 1.06; text-transform: uppercase; letter-spacing: 0.1em; transition: all 0.2s; }

.work-nav__item:hover { color: #848484; }

.work-nav__item + .work-nav__item:before { position: absolute; left: 0; top: 30px; height: 63px; width: 1px; background-color: #828281; content: ''; }

.work-nav__item.is-active { color: #b91443; background-color: #ffffff; z-index: 1; }

.work-nav__item.is-active:before { content: none; display: none; }

.work-nav__item.is-active:after { position: absolute; top: -8px; left: 0; right: 0; background-color: #ffffff; height: 8px; content: ''; }

/* Spec ----------------------------------- */
.spec { padding-top: 55px; padding-bottom: 100px; background-color: transparent; min-height: 670px; }

.spec__title { margin-bottom: 55px; color: #000000; }

.spec__block { position: relative; margin-left: 50%; padding: 50px 100px 60px 60px; color: #ffffff; background-color: rgba(185, 20, 67, 0.8); }

@media only screen and (max-width: 1439px) { .spec__block { padding-right: 50px; } }

.spec__block:before { content: ''; position: absolute; top: 0; left: 100%; bottom: 0; width: 2600px; background-color: rgba(185, 20, 67, 0.8); }

.spec__subtitle { padding-right: 20px; font-size: 26px; text-transform: uppercase; letter-spacing: 0.08em; }

.spec__text { margin-top: 30px; font-size: 18px; }

.spec__btn { margin-top: 45px; width: 244px; }

.spec__btn:hover { color: #b91443; background-color: #ffffff; border-color: #ffffff; }

/* Doc ----------------------------------- */
.desc { position: relative; opacity: 0; pointer-events: none; padding-top: 40px; padding-bottom: 40px; transition: all 0.2s; }

.desc.is-active { opacity: 1; pointer-events: auto; }

.desc .desc__arr { position: relative; }

.desc .desc__arr:before { content: ''; position: absolute; top: -39px; left: 10%; margin-top: -25px; margin-left: -30px; width: 0px; height: 0px; border-style: solid; border-width: 0 30px 25px 30px; border-color: transparent transparent #ffffff transparent; transition: all 0.2s; }

.desc[data-index='2'] .desc__arr:before { left: 30%; }

.desc[data-index='3'] .desc__arr:before { left: 50%; }

.desc[data-index='4'] .desc__arr:before { left: 70%; }

.desc[data-index='5'] .desc__arr:before { left: 90%; }

.desc__top { position: relative; text-align: center; letter-spacing: 0.08em; padding-bottom: 55px; }

.desc__prev { cursor: pointer; position: absolute; top: 25px; left: -20px; padding-left: 20px; padding-right: 20px; color: #b91443; transition: all 0.2s; }

.desc__prev:hover { color: #959595; }

.desc__next { cursor: pointer; position: absolute; top: 25px; padding-left: 20px; padding-right: 20px; right: -20px; color: #b91443; transition: all 0.2s; }

.desc__next:hover { color: #959595; }

.desc__title { margin-bottom: 20px; font-size: 30px; color: #b91443; text-transform: uppercase; letter-spacing: 0.24em; }

.desc__subtitle { font-family: "Calibri Bold", Arial, sans-serif; font-size: 16px; text-transform: uppercase; }

.desc__subtitle-normal { margin-left: 5px; font-family: "Calibri", Arial, sans-serif; }

.desc__bot { display: table; width: 100%; }

.desc__bot:after { content: ""; display: table; clear: both; }

.desc__col { display: table-cell; padding-right: 40px; vertical-align: top; height: 475px; }

.desc__col + .desc__col { padding-top: 50px; padding-bottom: 50px; padding-right: 0; border-top: 1px solid #ccc; border-bottom: 1px solid #ccc; }

.desc__col + .desc__col p { padding-right: 20px; }

.desc__col + .desc__col p + p { margin-top: 25px; }

.desc__col-in { overflow: auto; height: 375px; }

.desc__col-in::-webkit-scrollbar { width: 2px; }

.desc__col-in::-webkit-scrollbar-track { background-color: #e3e4e6; border: none; }

.desc__col-in::-webkit-scrollbar-thumb { background-color: #b91443; }

.desc__col-in::-webkit-scrollbar-thumb:hover { background-color: #b91443; }

.desc__img { max-width: none; }

.desc__close { display: block; margin-top: 40px; margin-left: auto; margin-right: auto; width: 216px; height: 50px; font-size: 16px; text-transform: uppercase; line-height: 50px; border: 1px solid rgba(0, 0, 0, 0.3); transition: all 0.2s; }

.desc__close:hover { color: #b91443; border-color: #b91443; }

.doc { padding-top: 110px; padding-bottom: 100px; background-color: transparent; min-height: 554px; }

.doc__container { position: relative; }

.doc__desc { position: absolute; z-index: 2; margin-top: -10px; top: 100%; left: 0; right: 0; margin-left: -80px; margin-right: -80px; background-color: #ffffff; box-shadow: 0 5px 5px rgba(0, 1, 1, 0.13); }

@media only screen and (max-width: 1800px) { .doc__desc { margin-left: 20px; margin-right: 20px; } }

.doc__list { margin-top: 65px; }

.doc__list:after { content: ""; display: table; clear: both; }

.doc__item { cursor: pointer; float: left; width: 20%; text-align: center; }

.doc__image { position: relative; margin-left: auto; margin-right: auto; margin-bottom: 45px; width: 223px; border-radius: 50%; overflow: hidden; }

.doc__image:before { content: ''; position: absolute; pointer-events: none; top: 7px; left: 7px; width: 206px; height: 206px; border-radius: 50%; border: 2px solid #fff; transition: all 0.2s; }

.doc__image:hover:before { border-color: #b91443; }

.doc__subtitle { margin-bottom: 20px; font-size: 19px; color: #b91443; text-transform: uppercase; letter-spacing: 0.24em; transition: all 0.2s; }

.doc__subtitle:hover { color: #000000; }

.doc__text { margin-left: auto; margin-right: auto; max-width: 180px; letter-spacing: 0.04em; }

/* Stat ----------------------------------- */
.stat { padding-top: 50px; font-family: "Calibri Light", Arial, sans-serif; background-color: transparent; min-height: 530px; }

.stat__title { text-indent: -140px; text-align: center; font-size: 18px; color: #ffffff; text-transform: uppercase; letter-spacing: 0.2em; }

.stat__title-number { text-indent: 0; display: inline-block; position: relative; top: 25px; margin-left: 35px; margin-right: 45px; font-size: 100px; letter-spacing: -0.04em; }

.stat__list { margin-top: 135px; padding-left: 50px; padding-right: 50px; }

.stat__list:after { content: ""; display: table; clear: both; }

.stat__item { float: left; width: 31%; padding-top: 10px; height: 270px; color: #b91443; text-align: center; background-color: #ffffff; }

@media only screen and (max-width: 1439px) { .stat__item { padding-left: 40px; padding-right: 40px; } }

.stat__item:nth-child(2) { position: relative; top: -25px; width: 38%; color: #ffffff; background-color: transparent; }

.stat__number { font-size: 100px; }

.stat__text { margin-top: 10px; letter-spacing: 0.2em; font-size: 18px; line-height: 1.22; text-transform: uppercase; }

/* Feed ----------------------------------- */
.feed { padding-top: 115px; padding-bottom: 160px; }

.feed__row { display: table; width: 100%; margin-bottom: 55px; }

.feed__col { display: table-cell; position: relative; width: 50%; padding-left: 35px; padding-right: 75px; }

.feed__item { margin-top: 55px; }

.feed__item:after { content: ""; display: table; clear: both; }

.feed__image { float: left; margin-right: 20px; width: 39px; height: 39px; padding: 3px; border-radius: 50%; box-shadow: 0 0 0 1px #f2d3b5; }

.feed__image-wrap { overflow: hidden; width: 100%; height: 100%; border-radius: 50%; }

.feed__content { overflow: hidden; line-height: 1.18; }

.feed__subtitle { position: relative; margin-bottom: 22px; font-size: 19px; color: #b91443; }

.feed__subtitle-date { color: #868686; }

.feed__subtitle:before { content: ''; position: absolute; left: 0; bottom: -11px; width: 42px; height: 2px; background-color: #b91443; }

.feed__text { color: #000; }

.feed__text-link { margin-left: 5px; color: #7083b9; text-decoration: underline; transition: all 0.2s; }

.feed__text-link:hover { color: #b91443; }

.feed__btn { display: block; margin-top: 20px; margin-left: auto; margin-right: auto; width: 216px; height: 50px; font-size: 16px; color: #000000; text-align: center; line-height: 50px; text-transform: uppercase; border: 1px solid rgba(0, 0, 0, 0.3); transition: all 0.2s; }

.feed__btn:hover { color: #b91443; border-color: #b91443; }

/* Request ----------------------------------- */
.request { padding-top: 90px; padding-bottom: 105px; background-color: transparent; min-height: 767px; }

.request__title { color: #ffffff; }

.form { margin-top: 45px; margin-left: auto; margin-right: auto; max-width: 595px; letter-spacing: 0.08em; }

.form__field { position: relative; }

.form__field:after { content: ""; display: table; clear: both; }

.form__field + .form__field { margin-top: 7px; }

.form__field_md { margin-top: 17px !important; }

.form__field_lg { margin-top: 36px !important; }

.form .icon-required { position: absolute; top: 0; left: 100%; margin-left: 15px; font-size: 30px; color: #ffffff; }

.form__input { display: block; padding: 0 13px; width: 100%; height: 40px; font-size: 16px; color: rgba(0, 29, 49, 0.9); line-height: 40px; background-color: #ffffff; border: 1px solid #d0d0d0; text-transform: uppercase; }

.form__input:-moz-placeholder, .form__input::-moz-placeholder { color: rgba(0, 29, 49, 0.9); opacity: 1; }

.form__input::-webkit-input-placeholder { color: rgba(0, 29, 49, 0.9); }

.form__input:focus ~ .form__tooltip { opacity: 1; }

.form__area { display: block; padding: 10px 13px; height: 125px; vertical-align: top; width: 100%; font-size: 16px; color: rgba(0, 29, 49, 0.9); line-height: 1.5; text-transform: uppercase; resize: none; border: 1px solid #d0d0d0; }

.form__area:-moz-placeholder, .form__area::-moz-placeholder { color: rgba(0, 29, 49, 0.9); opacity: 1; }

.form__area::-webkit-input-placeholder { color: rgba(0, 29, 49, 0.9); }

.form__tooltip { position: absolute; top: 0; left: 100%; padding: 15px 35px; white-space: nowrap; margin-left: 40px; font-size: 18px; line-height: 1; background-color: rgba(255, 255, 255, 0.5); border-radius: 5px; pointer-events: none; opacity: 0; letter-spacing: -0.02em; transition: all 0.2s; }

@media only screen and (max-width: 1439px) { .form__tooltip { padding-left: 20px; padding-right: 20px; width: 245px; white-space: normal; } }

.form__tooltip:before { position: absolute; left: -8px; top: 15px; width: 0px; height: 0px; border-style: solid; border-width: 8px 8px 8px 0; border-color: transparent rgba(255, 255, 255, 0.5) transparent transparent; content: ''; }

.form .previous-file { position: absolute; top: 100%; left: 0; display: block; color: #ffffff; width: 100%; }

.form .remove-btn { margin-left: 10px; color: #ffffff; }

.form .upload-btn { cursor: pointer; width: 218px; height: 43px; border: 1px solid white; font-size: 14px; color: #ffffff; line-height: 43px; text-transform: uppercase; transition: all 0.2s; }

.form .upload-button:hover .upload-btn { color: #b91443; background-color: #ffffff; }

.form input[type='file'] { cursor: pointer; }

.form__col { width: 50%; float: left; }

.form__check { display: none; }

.form__check-label { position: relative; display: block; margin-top: 2px; padding-top: 12px; float: right; padding-left: 5px; cursor: pointer; font-size: 18px; color: #ffffff; text-transform: uppercase; }

.form__check-label:before { position: absolute; top: 2px; left: -55px; width: 33px; height: 33px; background-color: #ffffff; border: 1px solid #d0d0d0; content: ''; }

.form__check-label:after { position: absolute; left: -45px; top: 15px; content: ''; width: 15px; height: 10px; background: url("../img/svg/check-original.svg") center center no-repeat; opacity: 0; transition: all 0.2s; }

.form__check:checked + .form__check-label:after { opacity: 1; }

.form__captcha { float: right; margin-right: 25px; width: 148px; height: 40px; }

.form__rel { position: relative; display: inline-block; }

.form__code { margin-left: 25px; width: 148px; }

.form__arrow { position: absolute; top: 9px; left: -10px; font-size: 18px; color: #ffffff; }

.form__submit { display: block; margin-left: auto; margin-right: auto; width: 244px; height: 50px; font-size: 16px; color: #ffffff; text-align: center; text-transform: uppercase; border: 1px solid #fff; transition: all 0.2s; }

.form__submit:hover { background-color: #ffffff; color: #b91443; }

.form .form-error { display: none; }

.form .form__error { position: absolute; top: 100%; left: 50%; transform: translateX(-50%); width: auto; margin-top: 20px; padding: 15px 35px; white-space: nowrap; font-size: 18px; line-height: 1; background-color: rgba(255, 255, 255, 0.5); border-radius: 5px; pointer-events: none; text-transform: uppercase; opacity: 0; color: #b91443; transition: all 0.2s; }

.form .form__error:before { content: ''; position: absolute; top: 0; left: 50%; margin-left: -8px; margin-top: -8px; width: 0px; height: 0px; border-style: solid; border-width: 0 8px 8px 8px; border-color: transparent transparent rgba(255, 255, 255, 0.5) transparent; }

.form .has-error ~ .form__field .form__error { opacity: 1; }

/* Contact ----------------------------------- */
.contact { padding-top: 100px; padding-bottom: 100px; }

.contact__title { margin-bottom: 60px; }

.contact__row:after { content: ""; display: table; clear: both; }

.contact__col { float: left; width: 50%; padding-right: 5px; height: 409px; overflow: hidden; }

.contact__col + .contact__col { padding-right: 0; }

.contact__img { height: 100%; width: auto; max-width: none; }

.contact__link { display: inline-block; text-indent: 5px; margin: 60px 40px 0; font-size: 18px; color: #b91443; text-decoration: underline; transition: all 0.2s; }

.contact__link:hover { color: #000000; }

.contact__info { font-family: "Calibri", Arial, sans-serif; text-align: center; font-size: 18px; color: #b91443; line-height: 1.5; text-transform: uppercase; }

#map { width: 100%; height: 409px; }

/* =================================== Other Styles @mixin ================================== */
.fadeInLeft2 { animation-name: fadeInLeft2; }

@-webkit-keyframes fadeInLeft2 { from { opacity: 0;
    transform: translate3d(-10%, 0, 0); }
  to { opacity: 1;
    transform: none; } }
